/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.Menu__text--secondaryColor {
  color: #ff4136; }

.Menu__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  opacity: 0.98;
  visibility: hidden;
  transition: all 0.4s ease; }
  .Menu__overlay--active {
    visibility: visible; }

.Menu__button {
  position: fixed;
  width: 5rem;
  right: 1rem;
  top: 1rem;
  z-index: 1100;
  border: 0.2rem solid #ccc;
  border-radius: 3px;
  padding: 0.2rem 1rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.4s ease; }
  .Menu__button--active {
    border: none; }

.Menu__label {
  color: #ccc;
  font-size: 1.3rem;
  transition: all 0.4s ease; }
  .Menu__label--active {
    opacity: 0; }

.Menu__branding {
  width: 100vw;
  height: 35vh;
  background: #333;
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  transform: translate(-100%);
  transition: all 0.4s ease; }
  .Menu__branding--active {
    transform: translate(0); }

.Menu__logo {
  border-top: 0.3rem solid #ccc;
  border-bottom: 0.3rem solid #ff4136;
  padding: 0; }

.Menu__icon {
  padding: 0.5rem;
  font-size: 2rem;
  color: #ccc;
  transition: all 0.4s ease; }
  .Menu__icon:hover {
    color: #ff4136; }

.Menu__nav {
  background: #262626;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  transform: translate(100%);
  transition: all 0.4s ease; }
  .Menu__nav--active {
    transform: translate(0); }

.Menu__ul {
  height: 100%;
  max-height: 500px;
  margin: 0;
  padding: 4rem 0;
  box-sizing: border-box;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }

.Menu__li {
  transform: translate(300rem);
  transition: all 0.4s ease; }
  .Menu__li:nth-child(1) {
    transition-delay: 0.1s; }
  .Menu__li:nth-child(2) {
    transition-delay: 0.2s; }
  .Menu__li:nth-child(3) {
    transition-delay: 0.3s; }
  .Menu__li:nth-child(4) {
    transition-delay: 0.4s; }
  .Menu__li:nth-child(5) {
    transition-delay: 0.5s; }
  .Menu__li--active {
    transform: translate(0); }

.Menu__link {
  color: #ccc;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.2rem;
  letter-spacing: 0.2rem;
  transition: all 0.4s ease; }
  .Menu__link:hover {
    color: #ff4136; }
  .Menu__link--selected {
    color: #ff4136; }

@media screen and (min-width: 768px) {
  .Menu__overlay {
    flex-direction: row; }
  .Menu__branding {
    width: 50vw;
    height: 100vh;
    transform: translate(-100%, 100%); }
    .Menu__branding--active {
      transform: translate(0); }
  .Menu__nav {
    transform: translate(100%, -100%); }
    .Menu__nav--active {
      transform: translate(0); } }
