/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.MyWork {
  margin-top: 5rem;
  margin-bottom: 5rem; }
  .MyWork__heading {
    font-size: 4.5rem;
    font-weight: 400;
    padding: 0;
    margin-top: 2rem;
    line-height: 1; }
  .MyWork__subHeading {
    font-weight: bold;
    line-height: 1.5; }
  .MyWork__text--secondaryColor {
    color: #ff4136; }
  .MyWork__suggestions {
    text-align: center; }
  .MyWork__tag {
    display: inline-block;
    background: #404040;
    color: #ccc;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
    margin: 0.1rem;
    padding: 0.2rem 0.5rem;
    border-radius: 1rem;
    cursor: pointer;
    transform: scale(0.9);
    transition: all 0.4s ease; }
    .MyWork__tag:hover {
      transform: scale(1); }
  .MyWork__selectedTag {
    display: inline-block;
    background: #404040;
    color: #ccc;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
    margin: 0.1rem;
    padding: 0.2rem 0.5rem;
    border-radius: 1rem;
    cursor: pointer;
    background: #ff4136;
    color: #111;
    font-weight: bold;
    margin: 0.35rem;
    transform: scale(1.1);
    transition: all 0.4s ease; }
    .MyWork__selectedTag:hover {
      transform: scale(1.1); }
  .MyWork__main {
    max-width: 1100px;
    margin: 5rem auto;
    display: grid;
    grid-template-columns: 1fr;
    align-items: stretch;
    grid-gap: 2.5rem;
    justify-items: center; }
    @media screen and (min-width: 768px) {
      .MyWork__main {
        grid-template-columns: repeat(2, 1fr); } }
    @media screen and (min-width: 1171px) {
      .MyWork__main {
        grid-template-columns: repeat(3, 1fr); } }
