/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.App__main {
  text-align: center;
  padding: 0 1rem; }
  @media screen and (min-width: 768px) {
    .App__main {
      text-align: left;
      padding: 0 2rem; } }

.App__footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 3rem;
  background: #2e2e2e;
  color: #bfbfbf;
  opacity: 0.9;
  box-shadow: -4px -4px 8px 0 rgba(0, 0, 0, 0.12), 0 -2px 4px 0 rgba(0, 0, 0, 0.08);
  display: flex;
  justify-content: flex-end;
  align-items: center; }

.App__licenseText {
  margin: 5px; }

.App__license {
  display: flex;
  margin: 5px; }
