body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.Home {
  width: 100%;
  height: 75vh;
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .Home__heading {
    font-size: 4.5rem;
    font-weight: 400;
    margin: 0rem;
    padding: 0rem;
    line-height: 1.3;
    border-top: 0.3rem solid #ccc;
    border-bottom: 0.3rem solid #ff4136; }
  .Home__subHeading {
    font-weight: bold;
    line-height: 1.5;
    margin-bottom: 0rem;
    padding-bottom: 0rem; }
  .Home__icon {
    padding: 0.5rem;
    font-size: 2.2rem;
    color: #ccc;
    transition: all 0.4s ease; }
    .Home__icon:hover {
      color: #ff4136; }
  .Home__cta {
    background-color: #ff4136;
    color: #ccc;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.2rem;
    letter-spacing: 0.2rem;
    margin: 2rem 0;
    padding: 0.5rem 1.5rem;
    border-radius: 3px;
    transition: all 0.4s ease; }
    .Home__cta:hover {
      transform: scale(1.1); }
  .Home__text--secondaryColor {
    color: #ff4136; }
