body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.Tech {
  margin-top: 5rem;
  padding-bottom: 5rem; }
  .Tech__heading {
    font-size: 4.5rem;
    font-weight: 400;
    padding: 0;
    margin-top: 2rem;
    line-height: 1; }
  .Tech__text--secondaryColor {
    color: #ff4136; }
  .Tech__servicesHeading {
    text-align: center;
    letter-spacing: 0.3rem;
    margin: 3rem; }
  .Tech__servicesGrid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly; }
  .Tech__service {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 1rem 1rem; }
  .Tech__serviceIcon {
    color: #ff4136;
    font-size: 3rem; }
  .Tech__serviceCaption {
    padding: 3px 0;
    margin: 0; }
  .Tech__serviceDetail {
    text-align: center; }
  .Tech__skillsGrid {
    display: grid;
    grid-gap: 1.5rem;
    margin-top: 2rem;
    grid-template-columns: 1fr;
    grid-template-areas: "fe" "be" "do"; }
    @media screen and (min-width: 768px) {
      .Tech__skillsGrid {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "fe be" "do do"; } }
  .Tech__skillsHeading {
    font-size: 1.05rem;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    background: #404040;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
    padding: 0 1rem; }
  .Tech__skillsBorder {
    border: none;
    background: #404040;
    border-bottom: 0.3rem solid #ff4136;
    border-top: 0.3rem solid #ff4136;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08); }
    .Tech__skillsBorder--frontEnd {
      grid-area: fe; }
    .Tech__skillsBorder--backEnd {
      grid-area: be; }
    .Tech__skillsBorder--devOps {
      grid-area: do; }
  .Tech__skills {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap; }
