/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.TechIcon {
  width: 3rem;
  margin: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .TechIcon__icon {
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    transition: all 0.4s ease; }
    .TechIcon__icon:hover {
      transform: scale(1.1); }
  .TechIcon__subtitle {
    text-align: center;
    color: #ccc;
    display: none; }

@media screen and (min-width: 500px) {
  .TechIcon {
    width: 4rem;
    margin: 1.2rem; }
    .TechIcon__icon {
      width: 4rem;
      height: 4rem; }
    .TechIcon__subtitle {
      display: block;
      margin-top: 0rem; } }
