/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.ContactMe {
  width: 100%;
  margin-top: 5rem;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media screen and (min-width: 768px) {
    .ContactMe {
      height: 75vh; } }
  .ContactMe__heading {
    font-size: 4.5rem;
    font-weight: 400;
    padding: 0;
    margin-top: 2rem;
    line-height: 1; }
  .ContactMe__subHeading {
    font-weight: bold;
    line-height: 1.5; }
  .ContactMe__text--secondaryColor {
    color: #ff4136; }
  .ContactMe__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center; }
  .ContactMe__contact {
    border: 0.3rem solid #ccc;
    margin: 1rem;
    padding: 0.75rem 1.5rem;
    transition: all 0.4s ease; }
    .ContactMe__contact:hover {
      padding: 0.5rem 1.2rem;
      background: #ff4136; }
      .ContactMe__contact:hover .ContactMe__text--secondaryColor {
        color: #111; }
