/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.Project {
  box-sizing: border-box;
  width: 280px;
  padding: 1rem;
  background: #404040;
  border-bottom: 0.3rem solid #ff4136;
  border-top: 0.3rem solid #ff4136;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: all 0.4s ease; }
  @media screen and (min-width: 500px) {
    .Project {
      width: 325px; } }
  @media screen and (min-width: 1171px) {
    .Project {
      width: 325px; } }
  .Project:hover {
    transform: scale(1.07); }
  .Project__heading {
    text-align: center;
    margin: 2rem auto; }
  .Project__image {
    display: block;
    width: 95%;
    margin: 0 auto; }
  .Project__description {
    text-align: center;
    margin: 1.5rem 0;
    flex-basis: 5rem; }
  .Project__buttons {
    display: flex;
    justify-content: space-around; }
    .Project__buttons a {
      text-decoration: none;
      color: #ccc;
      font-size: 1.2rem;
      transition: all 0.4s ease; }
      .Project__buttons a:hover {
        color: #ff4136; }
      .Project__buttons a-selected {
        color: #ff4136; }
