/**
* Base Settings
*/
body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.MenuButton {
  height: 1.8rem;
  width: 1.8rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  transition: all 0.4s linear;
  cursor: pointer; }
  .MenuButton--active {
    transform: rotate(calc(45deg * 3)); }
  .MenuButton__btnLine {
    height: 12%;
    background: #ccc;
    transition: all 0.5s linear; }
    .MenuButton__btnLine:nth-child(1) {
      animation: slideIn 0.5s 0s; }
    .MenuButton__btnLine:nth-child(2) {
      animation: slideIn 0.5s 0.1s; }
    .MenuButton__btnLine:nth-child(3) {
      animation: slideIn 0.5s 0.2s; }
    .MenuButton__btnLine--active:nth-child(1) {
      transform: translate(0, 275%);
      background: #ff4136; }
    .MenuButton__btnLine--active:nth-child(2) {
      filter: opacity(0%); }
    .MenuButton__btnLine--active:nth-child(3) {
      transform: translate(0, -275%) rotate(90deg);
      background: #ff4136; }

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translate(5rem); }
  to {
    transform: translate(0);
    opacity: 1; } }
