body {
  background: #333;
  color: #ccc;
  font-family: "Abel", serif;
  line-height: 1.8; }
  body::-webkit-scrollbar {
    width: 5px; }
  body::-webkit-scrollbar-track {
    background: #333;
    border-radius: 20px; }
  body::-webkit-scrollbar-thumb {
    background-color: #ff4136; }

.AboutMe {
  margin-top: 5rem;
  margin-bottom: 5rem; }
  .AboutMe__heading {
    font-size: 4.5rem;
    font-weight: 400;
    padding: 0;
    margin-top: 2rem;
    line-height: 1; }
  .AboutMe__subHeading {
    font-weight: bold;
    line-height: 1.5; }
  .AboutMe__text--secondaryColor {
    color: #ff4136; }
  .AboutMe__main {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: "portrait" "gh" "bio";
    grid-gap: 0rem 1rem;
    align-items: center;
    justify-items: center; }
    @media screen and (min-width: 768px) {
      .AboutMe__main {
        grid-template-columns: repeat(3, minmax(10px, 1fr));
        grid-template-areas: ". gh ." "portrait bio bio"; } }
  .AboutMe__gridHeading {
    grid-area: gh;
    justify-self: left;
    margin: 0; }
  .AboutMe__image {
    grid-area: portrait;
    height: 15rem;
    margin: 0 auto;
    font-size: 7rem;
    border: 0.5rem solid #ff4136;
    border-radius: 50%;
    overflow: hidden; }
    @media screen and (min-width: 768px) {
      .AboutMe__image {
        height: 12rem; } }
    .AboutMe__image img {
      filter: grayscale(0.5);
      height: 100%; }
  .AboutMe__bio {
    font-size: 1.2rem;
    grid-area: bio;
    text-align: left; }
